body {
    background-color: #21262A !important;
    font-family: ubuntu mono, Monaco;
    color: white;
}

.file {
    background-color: #282D33 !important;
    margin-bottom: 0;
    padding-bottom: 0;
    color: white;
    box-shadow: 3px 3px rgba(0, 0, 0, .2);
}

.filename {
    width: 200px;
    padding-left: 8px;
    border-left: #5189FB 3px solid;

    font-weight: 400;
    font-size: 1.25rem;
    
}
.title{
    color: white;
}
.input {
    padding-right: 0 !important;
}
textarea{
    resize: none !important;
    height: auto;
    overflow:hidden;
}
.txtarea {
    width: 100%;
    min-height: 50vh;
    height: auto;
    border: none;
    resize: vertical;
    margin-left: auto;
    margin-right: auto;
    background-color: #282D33;
    padding: 10px;
    color: white;
    
}
.txtarea:active, .txtarea:focus
{
    outline: 0px !important;
    -webkit-appearance:none;
    box-shadow: none !important;
}
.downSpacer{
    padding-bottom: 5px;
}